/* Exchange.css */

.container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    background-color: #f9f9f9;
    text-align: center;
  }
  
  .select-box {
    margin: 10px 0;
  }
  
  .input-box {
    margin: 10px 0;
  }
  
  .button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .result {
    font-size: 24px;
    font-weight: bold;
    margin-top: 20px;
  }
  